<template>
    <div>
    <!--  页首  -->
        <navig-bar></navig-bar>
    <!--  页面  -->
        <div>
            <van-tabs>
                <van-tab title="我的任务">
                    <!--搜索栏-->

                    <!--工单列表-->
                    <van-pull-refresh v-model="refreshingRepair_jobs" @refresh="onRefreshRepair_jobs">
                        <van-list
                                v-model="loadingRepair_jobs"
                                :finished="finishedRepair_jobs"
                                finished-text="没有更多了"
                                @load="onLoadRepair_jobs"
                        >
                            <van-cell v-for="item in listRepair_jobs" :key="item" :title="item.Repair_jobID" :value="item.Repair_jobUser" @click="showReply(item.Repair_jobItem)">
                                <template #icon>
                                    <van-image
                                            round
                                            width="45"
                                            height="45"
                                            src="http://imagemxc.test.upcdn.net//订单 (1).png"
                                    >
                                    </van-image>
                                </template>
                                <template #label>
                                    <div v-if="item.replied==false" style="color: #ff0000">待完成</div>
                                    <div v-else style="color: #00aa00">已完成</div>
                                </template>
                            </van-cell>
                        </van-list>
                    </van-pull-refresh>
                </van-tab>
            </van-tabs>
            <!--  工单说明部分  -->
            <van-popup v-model="showReplyPanel" position="bottom" @close="cancelReply" :style="{ height: '60%' }">
                <van-form @submit="submitReply">
                    <h4>工单详细内容</h4>
                    <div style="color: #A9A9A9; float:left; margin-left: 50px; font-size: 20px" v-html="thisItem"></div>
                    <van-button round block type="info" native-type="submit">
                        我已完成该任务
                    </van-button>
                </van-form>
            </van-popup>
        </div>
    <!--  页尾  -->
        <tab-bar></tab-bar>
    </div>
</template>

<script>
    import tabBar from "../tabBar1";
    import navigBar from "../navigBar1";
    import Vue from 'vue';
    import {Toast} from 'vant';
    Vue.use(Toast);
    export default {
        name: "Repair_job",
        components: {
            tabBar,
            navigBar
        },
        data(){
            return{
                repair_jobs: [
                    { Repair_jobID: 'R11111', Repair_jobUser: 'U11111111', Repair_jobItem: '新北区15-102，水龙头堵塞', replied: false},
                    { Repair_jobID: 'R22222', Repair_jobUser: 'U22222222', Repair_jobItem: '新北区15-205  空调失效' , replied: true},
                ],
                listRepair_jobs: [

                ],
                searchKey: '',
                loading: false,
                finished: false,
                refreshing: false,
                loadingRepair_jobs: false,
                finishedRepair_jobs: false,
                refreshingRepair_jobs: false,
                showReplyPanel: false,
                thisItem: '',
                editReply: '',
            }
        },
        methods: {
            onSearch(){
                this.list=[];
                for(let i=0;i<this.Repair_jobs.length; i++){
                    if(this.Repair_jobs[i].WorkOrderId.search(this.searchKey)!=-1)
                        this.list.push(this.Repair_jobs[i])
                }
            },
            onClear(){
                this.searchKey='';
                this.refreshing=true;
                this.onRefresh();
            },
            onLoad() {
                // 异步更新数据
                // setTimeout 仅做示例，真实场景中一般为 ajax 请求
                setTimeout(() => {
                    for (let i = 0; i < this.Repair_jobs.length; i++) {
                        this.list.push(this.Repair_jobs[i]);
                    }

                    // 加载状态结束
                    this.loading = false;
                    this.finished = true;

                    // 数据全部加载完成
                    // if (this.list.length >= this.actions.length) {
                    // this.finished = true;
                    // }
                }, 1000);
            },
            onRefresh() {
                if (this.refreshing) {
                    this.list = [];
                    this.refreshing = false;
                }

                // 清空列表数据
                this.finished = false;

                // 重新加载数据
                // 将 loading 设置为 true，表示处于加载状态
                this.loading = true;
                this.onLoad();
            },

            onSearchRepair_jobs(){
                this.listRepair_jobs=[];
                for(let i=0;i<this.repair_jobs.length; i++){
                    if(this.repair_jobs[i].Repair_jobID.search(this.searchKey)!=-1)
                        this.listRepair_jobs.push(this.repair_jobs[i])
                }
            },
            onClearRepair_jobs(){
                this.searchKey='';
                this.refreshingRepair_jobs=true;
                this.onRefreshRepair_jobs();
            },
            onLoadRepair_jobs() {
                // 异步更新数据
                // setTimeout 仅做示例，真实场景中一般为 ajax 请求
                setTimeout(() => {
                    for (let i = 0; i < this.repair_jobs.length; i++) {
                        this.listRepair_jobs.push(this.repair_jobs[i]);
                    }

                    // 加载状态结束
                    this.loadingRepair_jobs = false;
                    this.finishedRepair_jobs = true;

                    // 数据全部加载完成
                    // if (this.list.length >= this.actions.length) {
                    // this.finished = true;
                    // }
                }, 1000);
            },
            onRefreshRepair_jobs() {
                if (this.refreshingRepair_jobs) {
                    this.listRepair_jobs = [];
                    this.refreshingRepair_jobs = false;
                }

                // 清空列表数据
                this.finishedRepair_jobs = false;

                // 重新加载数据
                // 将 loading 设置为 true，表示处于加载状态
                this.loadingRepair_jobs = true;
                this.onLoadRepair_jobs();
            },
            showReply(item){
                this.thisItem=item;
                this.showReplyPanel = true;
            },
            cancelReply(){
                this.showReplyPanel = false;
            },
            submitReply(){
                this.showReplyPanel = false;
                this.refreshingRepair_jobs=true;
                this.onRefreshRepair_jobs();
                Toast.success("回复成功");
            }
        }
    }
</script>

<style scoped>

</style>



