<template>
  <div class="MyInfo">
    <div class="background">
      <img :src="imgSrc" width="100%" height="100%" alt="" />
    </div>

    <van-row type="flex" justify="end">
      <h2><a :href="islogin" @click="judge" style="color:blue;">{{myid}}</a></h2>
    </van-row>
    <h1>{{ msg }}</h1>
    <h2>你可以在这里投诉或者评价修理员的完成情况</h2>

    <div>
        <van-cell v-for="(item,index) in List" :key="index" clickable @click=comment(item)>
            {{List[index]}}
        </van-cell>
    </div>

    <van-popup v-model="show" closeable round :style="{height:'45%',width:'100%'}">
      <div style="margin-top:32px;">
        <h1>请提交您对这次维修任务的评价</h1>
        <van-rate v-model="value" touchable="true" />
        <van-field v-model="message" rows="2" autosize label="评价" type="textarea" maxlength="50" label-width="35px" placeholder="请输入评价" show-word-limit/>
        <form>
        <van-button type="info" @click="submit">提交评价</van-button>
        </form>
      </div>
    </van-popup>

    <div>
    <p>可以通过以下方式直接联系客服</p>
    客服电话:{{phone}} 客服邮箱:{{email}} 客服微信:{{weixin}}
    </div>
    

    <div style="margin-top:16px;">
        <Foot_bar></Foot_bar>
    </div>
  </div>
</template>

<script>
import Foot_bar from './Foot_bar';
import axios from 'axios';
import { Toast } from 'vant';

export default {
  name: 'Setting',
  data () {
    return {
      msg: '投诉/评价',
      message:'',
      islogin:'#/MyInfo',
      myid:localStorage.getItem('email'),
      List:['1','2','3'],
      value: 5,
      show:false,
      repairer:'',
      phone:'17815566221',
      email:'1322496098@qq.com',
      weixin:'17815566221',
      imgSrc:require('../assets/pugongying.jpg'),
    }
  },
  components:{
      Foot_bar
  },
  methods:{
      judge(){
          if(this.myid=="请先登入"){
              this.islogin="/";
          }
      },
      comment(name){
          this.show=true;
          this.repairer=name;
      },
      submit(){
          location.reload();
          this.$router.go(0);
          Toast('评价成功！');
      },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.background{
    width:100%;  
    height:100%;  /**宽高100%是为了图片铺满屏幕 */
    z-index:-1;
    position: fixed;
}
</style>



